.city-picker-container .input-wrapper {
  margin-top: 10px;
  padding: 5px 15px;
}
.city-picker-container .city-input {
  height: 36px;
  font-size: 20px;
  text-align: center;
  width: 100%;
  border-radius: 5px;
  background: #eae8eb;
}
.city-picker-container .wrapper-title {
  padding: 0 15px;
  height: 42px;
  line-height: 42px;
  background: #eae8eb;
}
.city-picker-container .city-item {
  height: 42px;
  line-height: 42px;
  padding: 0 30px;
}
.city-picker-container .cities.hot {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.city-picker-container .city-item.hot {
  width: calc(30vw - 5px);
  padding: 0;
  text-align: center;
}



